export default {
  data() {
    return {
      testimonialOption: {
        loop: true,
        speed: 950,
        navigation: {
          nextEl: '.hero-slider-nav.swiper-button-next',
          prevEl: '.hero-slider-nav.swiper-button-prev',
        },
      },
      testimonialData: []
    }
  },
  async mounted() {
    const testimonial = await this.$axios.get('/testimonial');
    this.testimonialData = testimonial.data
  }
}
